import "es6-promise/auto";
import Store from "@Scripts/store";
import getQueryParams from "@Scripts/helpers/GetQueryParams";
import Widget from "@Components/Widget.vue";

(function() {
  "use strict";

  // REQUIRED - save the lcs_payload in local storage
  localStorage.setItem("lcs_payload", getQueryParams("payload"));

  require("@Scripts/components");
  require("@Scripts/plugins");
  require("@Scripts/prototypes");

  new Vue({
    el: "#xerodesk",
    ...Widget,
    store: Store
  });
})();
