<template>
  <div class="body">
    <div id="scroll-to"></div>

    <Suggestions />
    <Messages />
    <Greeting />
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "ChatBody",
  computed: {
    ...mapState("message", ["messages"])
  },
  watch: {
    messages: {
      deep: true,
      handler: function() {
        setTimeout(() => {
          let scrollTo = this.$el.querySelector("#scroll-to");
          scrollTo.scrollIntoView({ behavior: "smooth" });
        }, 100);
      }
    }
  }
};
</script>
