<template>
  <div id="xerodesk">
    <div class="wrapper">
      <ChatFront v-if="['front'].includes(widget.operation)" />
      <ChatWelcome v-if="['list'].includes(widget.operation)" />

      <template v-if="['create', 'show'].includes(widget.operation)">
        <ChatHeader />
        <ChatBody />
        <ChatFooter />
      </template>
    </div>
  </div>
</template>

<script>
import { PUSHER_APP_CLUSTER, PUSHER_APP_KEY } from "@Scripts/constants";
import { mapState } from "vuex";
import CssVars from "css-vars-ponyfill";

export default {
  name: "Widget",
  data() {
    return {
      toggler: {
        open: "widget/toggler-open.svg",
        close: "widget/toggler-close.svg"
      }
    };
  },
  computed: {
    ...mapState("client", ["client"]),
    ...mapState("session", ["session"]),
    ...mapState("widget", ["widget"]),

    message: {
      get() {
        return this.$store.state.message.message;
      },
      set(newValue) {
        this.$store.state.message.message = newValue;
      }
    }
  },
  methods: {
    setupCssVars() {
      CssVars({
        variables: {
          ui_header_color: this.widget.ui_header_color,
          ui_client_bg: this.widget.ui_client_bg,
          ui_client_color: this.widget.ui_client_color,
          ui_robot_bg: this.widget.ui_robot_bg,
          ui_robot_color: this.widget.ui_robot_color,
          ui_color_primary: this.widget.ui_color_primary,
          ui_color_secondary: this.widget.ui_color_secondary
        }
      });
    },
    setupListeners() {
      const self = this;

      // set up pusher's listeners
      const PUSHER = new Pusher(PUSHER_APP_KEY, {
        cluster: PUSHER_APP_CLUSTER,
        encrypted: true
      });

      // channel to subscribe
      const CH_SESSION = PUSHER.subscribe("session");
      const CH_MESSAGE = PUSHER.subscribe("message");

      CH_SESSION.bind(`session.update`, data => {
        let { session } = data;

        self.$store.commit("session/UPDATE_SESSION", session);
      });

      CH_MESSAGE.bind(`message.from.admin`, data => {
        let { message, session, token } = data;

        // make sure the message belongs to the client
        if (self.client.token == token) {
          // push the message if you are viewing the session
          if ("session" in self.session && self.session.session == session) {
            // mart it as read
            axios.put(`/plugin/session/${session}/seen`);

            self.$store.commit("message/PUSH_MESSAGE", { ...message, attachments: [] });
          } else {
            // if you do not select any session at the moment
            self.$store.commit("session/PUSH_MESSAGE", { session, message });
          }
        }
      });

      CH_MESSAGE.bind(`attachment.created`, async data => {
        let { attachment, client, message, session } = data;

        self.$store.commit("message/INSERT_ATTACHMENT", {
          ...message,
          attachment
        });
      });
    }
  },
  mounted() {
    this.setupCssVars();
    this.setupListeners();
  }
};
</script>
