<template>
  <div class="footer" ref="toggler" autocomplete="off">
    <Emoji v-if="picker" @selectEmoji="selectEmoji" />
    <Attachment v-if="attachments.length" :attachments="attachments" @removeAttachment="attachments = $event" />
    <ReplyTo v-if="reply_to" :reply_to="reply_to" />

    <div class="footer-form">
      <div class="footer-input">
        <button type="button" @click="picker = !picker">
          <InlineSvg name="heroicons/emoji-happy.svg" color="#c4c6c6" size="22px" />
        </button>

        <input type="text" class="input" v-model="message" @keyup.enter="submitChat()" placeholder="Write something..." />
        <!-- <Editor v-model="message" :emoji="emoji" @onEnter="submitChat()" /> -->
      </div>

      <div class="footer-control">
        <input type="file" ref="file" @change="onFileChange" :accept="accept" multiple />

        <button type="button" @click="$refs.file.click()" style="transform: rotate(-45deg);">
          <InlineSvg name="heroicons/paper-clip.svg" color="#c4c6c6" size="20px" />
        </button>

        <button type="button" @click="submitChat()" style="margin-left: 3px;">
          <InlineSvg name="mdi/send.svg" color="#f68a1f" size="23px" />
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { nanoid } from "nanoid";
import Editor from "./Editor.vue";

export default {
  name: "ChatFooter",
  components: { Editor },
  data: () => ({
    accept: "text/csv,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document,image/gif,text/html,image/vnd.microsoft.icon,image/jpeg,image/png,application/vnd.ms-powerpoint,application/vnd.openxmlformats-officedocument.presentationml.presentation,application/vnd.rar,text/plain,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/zip,application/x-7z-compressed",
    extensions: ["csv", "doc", "docx", "gif", "htm", "html", "ico", "jpeg", "jpg", "png", "pdf", "pptx", "rar", "txt", "xls", "xlsx", "zip", "7z"],
    emoji: "",
    picker: false,
    message: "",
    attachments: [],
    reply_to: null
  }),
  computed: {
    ...mapState("client", ["client"]),
    ...mapState("message", ["reply_to_shadow"]),
    ...mapState("widget", ["widget"]),

    operation: {
      get() {
        return this.$store.state.widget.widget.operation;
      },
      set(newValue) {
        this.$store.state.widget.widget.operation = newValue;
      }
    }
  },
  methods: {
    selectEmoji(e) {
      this.picker = false;
      this.emoji = e;
    },
    async onFileChange(e) {
      [...e.target.files].forEach(file => {
        this.attachments.push({
          file,
          ext: file.name.split(".").pop()
        });
      });
    },
    async submitChat() {
      if (this.message != "<p></p>") {
        this.$store.state.message.message = this.message;
        this.$store.state.message.message_attachments = this.attachments;
        this.$store.state.message.reply_to = this.reply_to;

        this.message = "";
        this.attachments = [];
        this.$store.state.message.reply_to_shadow = null;

        // if starting a session
        if (this.operation == "create") {
          // save the session first
          let { data } = await axios.post(`/plugin/session`, {
            client_id: this.client.id
          });

          // save to local storage
          localStorage.setItem("lcs_session", data.session);

          // set the session in store
          this.$store.state.session.session = { ...data, messages: [] };

          // push to the client for history of WelcomeComponent
          this.$store.commit("session/PUSH_SESSION", { ...data, messages: [] });

          // set the operation to show for further reply
          this.operation = "show";
        }

        // if message is not empty
        if (this.$store.state.message.message) {
          // message details
          let message = { hash: nanoid(), sender: "client", message: this.$store.state.message.message };
          if (this.$store.state.message.reply_to) message.reply_to = this.$store.state.message.reply_to;

          // push the message immediately for real-time experience
          this.$store.commit("message/PUSH_MESSAGE", {
            ...message,
            created_at: new Date(),
            attachments: []
          });

          // then save the message in background
          await axios
            .post(`/plugin/message`, {
              ...message,
              client_id: this.client.id,
              session: localStorage.getItem("lcs_session"),
              user_id: null
            })
            .then(({ data }) => {
              this.$store.commit("message/UPDATE_MESSAGE", data.data);
            });
        }

        // if there are attachment, process it
        if (this.$store.state.message.message_attachments) this.handleFile();
      }
    },
    async handleFile() {
      let attachments = this.$store.state.message.message_attachments;

      await Promise.all(
        attachments.map(async attachment => {
          let form = new FormData();
          let file = attachment.file;
          let headers = { "Content-Type": "multipart/form-data" };
          let message = { hash: nanoid(), sender: "client", message: "" };
          if (this.$store.state.message.reply_to) message.reply_to = this.$store.state.message.reply_to;

          if (file.size < 20000000 && this.extensions.includes(attachment.ext.toLowerCase())) {
            form.append("id", this.client.id);
            form.append("extension", file.name.split(".").pop());
            form.append("session", localStorage.getItem("lcs_session"));
            form.append("name", file.name.split(".").slice(0, -1).join(".")); // prettier-ignore
            form.append("file", file);

            // push the message immediately for real-time experience
            this.$store.commit("message/PUSH_MESSAGE", {
              ...message,
              created_at: new Date(),
              attachments: []
            });

            // then save the attachments in background
            await axios
              .post(`/plugin/message`, {
                ...message,
                client_id: this.client.id,
                session: localStorage.getItem("lcs_session")
              })
              .then(response => {
                this.$store.commit("message/UPDATE_MESSAGE", response.data.data);

                // process the attachment
                axios.post(`/plugin/message/${response.data.data.hash}/attachment`, form, headers);
              });
          }
        })
      );
    }
  },
  watch: {
    "$store.state.message.message_suggestion": function(newVal) {
      if (this.widget.operation == "create") {
        this.message = newVal;
        this.submitChat();
      }
    },
    reply_to_shadow(newVal) {
      this.reply_to = newVal;
    }
  }
};
</script>
