const namespaced = true;

const state = {
  agent: {},
  widget: {
    ui_header_title: "Live Support",
    ui_header_subtitle: "Need help? We're online!",
    ui_header_color: "#ffffff",
    ui_client_bg: "#d0d0d0",
    ui_client_color: "#424242",
    ui_robot_bg: "#2d3795",
    ui_robot_color: "#ffffff",
    ui_color_primary: "#99be3c",
    ui_color_secondary: "#f68a1f",

    operation: "front",
    ready: false,
    type: "live"
  },
  is_open: true,
  is_loading: false
};

const getters = {};

const mutations = {
  SET_AGENT: (state, agent) => (state.agent = agent),

  SET_WIDGET_STATUS: (state, status) => (state.widget.ready = status)
};

const actions = {};

export default {
  namespaced,
  state,
  mutations,
  getters,
  actions
};
