<template>
  <div class="body-greeting" v-if="!$isEmpty(messages)">
    <img :src="timeImage()" />
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Greeting",
  data: () => ({
    colorPrimary: "red"
  }),
  computed: {
    ...mapState("message", ["messages"])
  },
  methods: {
    timeGreeting() {
      let currentDay = new Date();
      let currentHour = currentDay.getHours();
      let timeGreeting = "Good day";

      if (currentHour < 12) {
        timeGreeting = "Good Morning";
      } else if (currentHour < 18) {
        timeGreeting = "Good Afternoon";
      } else {
        timeGreeting = "Good Evening";
      }

      return timeGreeting;
    },
    timeImage() {
      let greetingImage;
      let timeGreetingResult = this.timeGreeting();

      if (timeGreetingResult === "Good Morning") greetingImage = "season-morning.svg";
      if (timeGreetingResult === "Good Afternoon") greetingImage = "season-afternoon.svg";
      if (timeGreetingResult === "Good Evening") greetingImage = "season-evening.svg";

      return `${this.$IMAGE_URL}/widget/${greetingImage}`;
    }
  }
};
</script>
