import asyncLocalStorage from "@/js/helpers/AsyncLocalStorage";

const namespaced = true;

const state = {
  client: {},
  client_is_loading: false
};

const getters = {};

const mutations = {
  SET_LOADING: (state, status) => (state.client_is_loading = status),

  SET_CLIENT: (state, client) => (state.client = client)
};

const actions = {
  async fetch({ commit }, token) {
    commit("SET_LOADING", true);

    // fetch the client
    let { data } = await axios.get(`/plugin/client/${token}`);
    commit("SET_CLIENT", data);

    // store the token
    await asyncLocalStorage.setItem("lcs_token", token);

    commit("SET_LOADING", false);
  },

  async create({ commit }, payload) {
    commit("SET_LOADING", true);

    // create the client
    let { data } = await axios.post(`/plugin/client`, payload);
    commit("SET_CLIENT", data);

    // store the token
    await asyncLocalStorage.setItem("lcs_token", data.token);

    commit("SET_LOADING", false);
  }
};

export default {
  namespaced,
  state,
  mutations,
  getters,
  actions
};
