<template>
  <div class="body-messages" v-if="['create', 'show'].includes(widget.operation)">
    <template v-for="(message, i) in sorted">
      <template v-if="['client', 'admin'].includes(message.sender)">
        <div class="bubble" :class="{ 'from-client': message.sender == 'client', 'from-admin': message.sender == 'admin' }" :key="i">
          <div class="bubble-content">
            <div class="msg-actions">
              <button type="button" class="action" @click="onReply(message)">
                <InlineSvg :name="`heroicons/reply.svg`" color="#000" size=".75rem" />
              </button>
              <button type="button" class="action" @click="onDownload(message)" v-show="!$isEmpty(message.attachments)">
                <InlineSvg :name="`mdi/download.svg`" color="#000" size=".75rem" />
              </button>
            </div>
            <div class="msg-group">
              <ReplyingTo v-if="message.reply_to" :message="message" />
              <div class="msg-dts" v-if="!$isEmpty(message.message)" v-html="message.message"></div>
              <div class="msg-att" v-if="!$isEmpty(message.attachments)">
                <div class="msg-att-item" v-for="attachment in message.attachments" :key="attachment.id">
                  <div class="msg-att-item-image" v-if="['ico', 'jpeg', 'jpg', 'png'].includes(attachment.extension.toLowerCase())">
                    <img :src="`${$BASE_URL}/storage/uploads/clients/${client.token}/${session.session}/${attachment.name}.${attachment.extension}`" />
                  </div>
                  <div class="msg-att-item-file" v-else>
                    <div class="msg-att-item-icon" @click="downloadAtt(attachment)">
                      <InlineSvg :name="`heroicons/${getIcon(attachment.extension.toLowerCase())}.svg`" color="#000" size="25px" />
                    </div>
                    <div class="msg-att-item-name" @click="downloadAtt(attachment)">
                      <span>{{ `${attachment.name}.${attachment.extension}` }}</span>
                    </div>
                  </div>
                </div>
              </div>
              <Spinner v-if="$isEmpty(message.message) && $isEmpty(message.attachments)" />
            </div>
          </div>
        </div>
      </template>

      <template v-else>
        <template v-if="message.sender == 'session'">
          <div class="bubble session" :key="i">
            <div class="bubble-message" v-html="message.message"></div>
          </div>
        </template>
        <template v-if="message.sender == 'error'">
          <div class="bubble error" :key="i">
            <div class="bubble-content">
              <template v-if="$IS_DEVELOPMENT">
                <div class="msg-group" v-html="message.message"></div>
              </template>

              <template v-else>
                <div class="msg-group">Oppss! Something went wrong. Please contact the system administrator.</div>
              </template>
            </div>
          </div>
        </template>
      </template>
    </template>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Messages",
  computed: {
    ...mapState("client", ["client"]),
    ...mapState("session", ["session"]),
    ...mapState("widget", ["widget"]),
    ...mapState("message", ["messages"]),

    sorted() {
      let messages = this.messages;

      return messages.sort((a, b) => b.created_at - a.created_at);
    }
  },
  methods: {
    getIcon(ext) {
      // documents
      if (["doc", "docx", "htm", "html", "pdf", "txt", "pptx"].includes(ext)) return "document-text";

      // spread sheet
      if (["csv", "xls", "xlsx"].includes(ext)) return "document-report";

      // images
      if (["gif", "ico", "jpeg", "jpg", "png"].includes(ext)) return "photograph";

      // compressed
      if (["rar", "zip", "7z"].includes(ext)) return "folder";
    },
    downloadAtt(att) {
      let url = `${this.$BASE_URL}/storage/uploads/clients/${this.client.token}/${this.session.session}/${att.name}.${att.extension}`;
      let link = document.createElement("a");

      // if you don't know the name or want to use the webserver default set name = ''
      link.setAttribute("download", "");
      link.setAttribute("target", "_blank");
      link.href = url;

      document.body.appendChild(link);
      link.click();
      link.remove();
    },
    onReply(message) {
      this.$store.state.message.reply_to_shadow = message.id;
    },
    onDownload(message) {
      this.downloadAtt(message.attachments[0]);
    }
  }
};
</script>
