<template>
  <div v-if="widget.operation == 'create'">
    <p>Hello, there! 👋 Anything we can do for you? Let us know your question and we will get back to you as soon as possible.</p>

    <ul class="suggestion-list">
      <li class="suggestion-list--item" v-for="s in suggestions" :key="s.id">
        <button @click="message_suggestion = s.body" v-html="s.body"></button>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Suggestions",
  data: () => ({
    suggestions: []
  }),
  computed: {
    ...mapState("widget", ["widget"]),

    message_suggestion: {
      get() {
        return this.$store.state.message.message_suggestion;
      },
      set(newValue) {
        this.$store.state.message.message_suggestion = newValue;
      }
    }
  },
  mounted() {
    this.suggestions = [
      {
        id: 1,
        body: "<p>Access</p>"
      },
      {
        id: 2,
        body: "<p>Application</p>"
      },
      {
        id: 3,
        body: "<p>Network</p>"
      },
      {
        id: 4,
        body: "<p>Hardware</p>"
      },
      {
        id: 5,
        body: "<p>Security Incident</p>"
      },
      {
        id: 6,
        body: "<p>Telephony</p>"
      }
    ];
  }
};
</script>
