const namespaced = true;

const state = {
  session: {},
  sessions: []
};

const getters = {
  sessions: state => {
    return state.sessions
      .map(s => {
        let unread_count = 0;

        // count the unreads
        s.messages.forEach(m => {
          if (m.sender != "client" && m.is_read == false) unread_count += 1;
        });

        return {
          ...s,
          unread_count
        };
      })
      .reverse();
  }
};

const mutations = {
  SET_SESSIONS: (state, sessions) => (state.sessions = sessions),

  PUSH_MESSAGE: (state, payload) => {
    state.sessions = state.sessions.map(s => {
      if (s.session == payload.session) {
        let messages = s.messages.push(payload.message);

        return {
          ...s,
          ...messages
        };
      }

      return s;
    });
  },

  PUSH_SESSION: (state, session) => state.sessions.push(session),

  UPDATE_SESSION: (state, session) => {
    state.sessions = state.sessions.map(s => {
      if (s.id == session.id) {
        return {
          ...s,
          ...session
        };
      }

      return s;
    });
  }
};

const actions = {
  async fetch({ commit }) {
    let token = localStorage.getItem("lcs_token");

    // fetch session of client
    let { data } = await axios.get(`/plugin/client/${token}/sessions`);
    commit("SET_SESSIONS", data);
  }
};

export default {
  namespaced,
  state,
  mutations,
  getters,
  actions
};
