const files = require.context("../", true, /\.vue$/i);

files.keys().map(key => {
  // prettier-ignore
  // let name = key.split("/").pop().split(".")[0] // name base on filename
  let name = files(key).default.name; // name base on declared from the component itself

  Vue.component(name, files(key).default);
});
