<template>
  <div class="welcome">
    <div class="welcome-banner">
      <div class="banner-icons">
        <img class="logo" :src="`${$IMAGE_URL}/icon.png`" alt="Widget Icon" />

        <button class="lock" @click="logout">
          <InlineSvg :name="`heroicons/lock-closed.svg`" color="#fff" size=".75rem" />
        </button>
      </div>

      <div class="banner-titles">
        <p class="title">Welcome, we're Xerodesk.</p>
        <p class="sub-title">We connect customers to businesses.</p>
      </div>
    </div>

    <div class="welcome-create">
      <p class="mt-0 mb-5">Stuck on something? We're here to help!</p>

      <button type="button" class="btn btn-secondary btn-block" @click="startSession">
        Start a conversation
      </button>
    </div>

    <div class="welcome-ticket">
      <p class="history">Your ticket history:</p>

      <div class="list" v-for="s in sessions" :key="s.session" @click="setSession(s)">
        <div class="list-meta">
          <p class="title">{{ s.session }}</p>
          <p class="date">{{ $dayjs("fromNow", s.created_at) }}</p>
        </div>
        <div class="list-status">
          <p class="counter" v-if="s.unread_count > 0">{{ s.unread_count }}</p>
          <p class="status">{{ statuses.find(st => st.id == s.status).name }}</p>
        </div>
      </div>

      <p class="no-result" v-show="$isEmpty(sessions)">No ticket found.</p>
    </div>

    <div class="welcome-powered">
      Powered by Xerodesk
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";

export default {
  name: "ChatWelcome",
  data: () => ({
    statuses: [
      { id: 1, name: "Open" },
      { id: 2, name: "Pending" },
      { id: 3, name: "Resolved" },
      { id: 4, name: "Closed" },
      { id: 5, name: "Waiting for Agent" },
      { id: 6, name: "Waiting for Client" }
    ]
  }),
  computed: {
    ...mapState("widget", ["widget"]),
    ...mapState("client", ["client"]),
    ...mapGetters("session", ["sessions"])
  },
  methods: {
    startSession() {
      // clear all the messages
      this.$store.state.message.messages = [];

      // remove the previous session
      localStorage.removeItem("lcs_session");

      // set the operation to create
      this.widget.operation = "create";
    },
    async setSession(s) {
      // set the selected session
      localStorage.setItem("lcs_session", s.session);

      // clear all the messages
      this.$store.state.message.messages = [];

      // mark read all unread messages
      let { data } = await axios.put(`/plugin/session/${s.session}/seen`);

      // set the session in store
      this.$store.state.session.session = data;

      // get the messages
      this.$store.dispatch("message/fetchMessages", data.session);

      // fetch the messages of the session
      this.$store.commit("message/SET_MESSAGES", data.messages);

      // update the session from sessions
      this.$store.commit("session/UPDATE_SESSION", data);

      // set the operation to show
      this.widget.operation = "show";
    },
    logout() {
      localStorage.removeItem("lcs_token");
      this.widget.operation = "front";
    }
  },
  created() {
    this.$store.dispatch("session/fetch");
  }
};
</script>
