// asynchronous implementation of local storage
// eg. asyncLocalStorage.setItem("param3", "http://example.com?param1=1&param2=2&param3=3");
// eg. asyncLocalStorage.getItem("param3");

export default {
  setItem: async function(key, value) {
    await null;
    return localStorage.setItem(key, value);
  },
  getItem: async function(key) {
    await null;
    return localStorage.getItem(key);
  },
  removeItem: async function(key) {
    await null;
    return localStorage.removeItem(key);
  }
};
