import { BASE_URL, IS_DEVELOPMENT } from "@Scripts/constants";

Vue.prototype.$BASE_URL = BASE_URL;
Vue.prototype.$IMAGE_URL = BASE_URL + "/images";
Vue.prototype.$IS_DEVELOPMENT = IS_DEVELOPMENT;
Vue.prototype.$isEmpty = require("lodash/isEmpty");
Vue.prototype.$delay = async function(ms) {
  return await new Promise(resolve => {
    setTimeout(() => {
      resolve();
    }, ms);
  });
};
Vue.prototype.$dayjs = function(trans, date, data) {
  let dayjs = require("dayjs");
  let relativeTime = require("dayjs/plugin/relativeTime");
  dayjs.extend(relativeTime);

  let result;

  if (trans == "format") result = dayjs(date).format(data);
  if (trans == "fromNow") result = dayjs(date).fromNow();

  return result;
};
