// get the parameter from the URL provided
// eg. getQueryParams("param3", "http://example.com?param1=1&param2=2&param3=3");

export default function(params, url = window.location.href) {
  let regexp = new RegExp("[?&]" + params + "=([^&#]*)", "i"); // this is an expression to get query strings
  let qString = regexp.exec(url);

  return qString ? qString[1] : null;
}
